import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Leagues from '@/pages/Leagues'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/leagues',
      name: 'Leagues',
      component: Leagues
    }
  ]
})



